@import url(https://fonts.googleapis.com/css?family=Open+Sans+Condensed:300&display=swap);
.navbar__logo{width:30%;max-width:12rem;align-self:center}

.navbar__links{display:none}@media (min-width: 768px){.navbar__links{display:flex;width:70%;justify-content:flex-end;flex-wrap:wrap;list-style:none;-webkit-padding-start:10px;padding-inline-start:10px}.navbar__links li{align-self:center;padding:0 0.2rem}.navbar__links li a{color:#000;text-decoration:none}.navbar__links li a:hover,.navbar__links li a:active{color:darkgrey;padding-bottom:0.4rem;border-bottom:2px solid black;cursor:pointer}}@media (min-width: 768px) and (min-width: 920px){.navbar__links li a{margin-right:1rem}}

.burger{display:flex;position:absolute;top:3%;right:2rem;flex-direction:column;justify-content:space-around;width:2rem;height:2rem;background:transparent;border:none;z-index:2}.burger div{display:flex;height:0.25rem;background:#000;border-radius:10px;transition:all 0.3s linear;position:relative;transform-origin:1px}.burger--open{position:fixed}.burger--open div{background:#fff}.burger--open div:first-child{transform:rotate(45deg)}.burger--open div:nth-child(2){opacity:0;transform:translateX(20px)}.burger--open div:nth-child(3){transform:rotate(-45deg)}@media (min-width: 768px){.burger{display:none}}

.sidebar{display:flex;transform:translateX(100%);flex-direction:column;justify-content:center;background:#000;padding:1rem;position:fixed;top:0;left:0;transition:transform 0.3s ease-in-out;width:100vw;height:100vh;z-index:1}.sidebar--open{transform:translateX(0)}.sidebar li{margin-top:2rem}.sidebar a{font-size:1.5rem;text-transform:uppercase;margin:2rem 0;font-weight:bold;letter-spacing:0.5rem;color:#fff;text-decoration:none;transition:color 0.3s linear;text-align:center}@media (min-width: 768px){.sidebar a{display:none}}

.navbar{margin:0 .5rem;display:flex;justify-content:space-between}@media (min-width: 920px){.navbar{margin:0 6rem}}

.intro{display:flex;flex-direction:column-reverse;background-size:cover;background:url(/simple-portfolio/static/media/intro.7408cbac.jpg) center;min-height:60vh}.intro__title{background:white;border-radius:15px;text-align:center;padding:0.5rem 0.5rem 0.5rem 1rem;color:#ba094e;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.intro__article{padding:2rem}.intro__paragraph__bold{font-weight:bolder}.intro__paragraph__bold span{color:#ba094e;padding-left:4px}@media (min-width: 920px){.intro{margin:0 0 0 3rem;flex-direction:row;background:none;align-items:center}.intro__title{background:none;padding:0;font-size:6vw;text-align:left}.intro__article{width:50%}.intro__img{width:50%;height:65vh;padding-left:5%;background-image:url(/simple-portfolio/static/media/intro.7408cbac.jpg);background-size:cover;background-position:left bottom}}

.section-title{display:flex;justify-content:center;color:#000;font-size:3rem;margin:0;padding:4rem 0 3rem 0}

.btn{color:#fff;background:#212020;cursor:pointer;transition:0.2s;border:1px solid transparent;border-bottom:1px solid darkgrey;padding:10px}.btn a{text-decoration:none;color:#fff}.btn:hover,.btn :active{border:1px solid white;border-radius:50px}.btn--project{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.btn--blog{color:#fff;background:#212020;cursor:pointer;transition:0.2s;border:1px solid transparent;border-bottom:1px solid darkgrey;padding:10px}.btn--blog:hover,.btn--blog :active{border:1px solid white;border-radius:50px}.btn--form{margin-left:.5rem;width:30%}

.blog__card{margin:1rem 0;justify-content:center;background:#212020;border-radius:30px;color:#fff;padding:2rem;width:80%}.blog__title{font-size:2rem;text-align:center;-webkit-margin-before:0;margin-block-start:0;-webkit-margin-after:0;margin-block-end:0}@media (min-width: 920px){.blog__card{max-width:340px;min-height:300px;transition:0.5s}.blog__card:hover,.blog__card:active{transform:scale(1.03)}}

.blog{display:flex;flex-direction:column;align-items:center}@media (min-width: 920px){.blog{flex-direction:row;justify-content:space-evenly;flex-wrap:wrap}}

