tion:.8s;-webkit-animation-delay:-.6s;animation-delay:-.6s;-webkit-animation-direction:alternate;animation-direction:alternate;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite}@-webkit-keyframes wiggle-uc1421095{from{transform:rotateZ(10deg)}to{transform:rotateZ(-10deg)}}@keyframes wiggle-uc1421095{from{transform:rotateZ(10deg)}to{transform:rotateZ(-10deg)}}#tech-icon-Python{-webkit-animation-name:wiggle-ub84fe138;animation-name:wiggle-ub84fe138;-webkit-animation-duration:.8s;animation-duration:.8s;-webkit-animation-delay:.3s;animation-delay:.3s;-webkit-animation-direction:alternate;animation-direction:alternate;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite}@-webkit-keyframes wiggle-ub84fe138{from{transform:rotateZ(10deg)}to{transform:rotateZ(-10deg)}}@keyframes wiggle-ub84fe138{from{transform:rotateZ(10deg)}to{transform:rotateZ(-10deg)}}

.tech__text{font-weight:600;background-color:#000;color:#fff;padding:15px;border-radius:1rem;display:inline-block;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;margin:5px}

.tech{margin:0 2rem;display:grid;grid-template-columns:repeat(auto-fit, minmax(130px, 1fr));justify-items:center}.tech__no-logo{display:flex;flex-wrap:wrap;justify-content:center;align-content:center}@media (min-width: 768px){.tech{grid-template-columns:repeat(auto-fit, minmax(200px, 1fr))}}@media (min-width: 920px){.tech{grid-template-columns:repeat(4, 1fr)}}

.project__card{padding:1rem 0}.project__img{display:flex;align-items:center;padding-bottom:1rem}.project__img img{height:240px;width:300px;border-radius:10px}.project__article h3{font-size:2rem;margin:0 0 1rem 0;-webkit-margin-before:0;margin-block-start:0}.project__title{color:#fff;font-style:600}.project__sub{font-weight:600;font-style:italic;margin:0;color:#fff}@media (min-width: 768px){.project__img{padding-bottom:0}.project__img img{height:350px}}@media (min-width: 920px){.project__card{display:flex;flex-direction:row;justify-content:center}.project__img{max-width:30vw;max-height:30vw;padding-bottom:0}.project__img img{width:100%}.project__article{max-width:50vw;padding:0 1rem;display:flex;flex-direction:column;justify-content:center}}

.project{color:#fff;background:#ba094e;padding:2rem;margin:0}

.contact-container{display:flex;justify-content:center}.contact-card{display:flex;flex-direction:column;align-items:center;min-width:-webkit-fit-content;min-width:-moz-fit-content;min-width:fit-content;max-width:50vw;padding:0 1rem;margin-top:-2rem}@media (min-width: 920px){.contact-card{flex-direction:row;justify-content:center}}.contact-picture{padding:0 1rem}.contact-picture img{width:100%;border-radius:1rem}@media (min-width: 920px){.contact-picture{width:25%}}.contact-text{display:flex;flex-direction:column;align-items:center}@media (min-width: 920px){.contact-text{align-items:flex-start}}.contact-name{font-weight:600;font-size:2.5rem;-webkit-margin-before:0;margin-block-start:0;-webkit-margin-after:0;margin-block-end:0;padding-top:2rem}@media (min-width: 920px){.contact-name{padding-top:0;padding-left:2rem}}.contact-details{display:flex}.contact-details-base{color:#ba094e;margin-left:2rem}.contact-details-tail{padding-left:1rem}.contact-logo{display:flex}.contact-logo a{text-decoration:none;padding-right:1rem}.contact-logo i{color:#000;font-size:2.5rem}@media (min-width: 920px){.contact-logo{margin-left:-4.4rem}}

.footer{align-items:center;margin:1rem}.footer__logo{display:flex;justify-content:center;margin:5px 0}.footer__logo a{text-decoration:none;padding-left:1rem}.footer__logo i{color:#000;font-size:2.5rem}.footer__text{padding:0 1rem;font-weight:bold;align-items:center;display:flex;flex-direction:column}@media (min-width: 768px){.footer{display:flex;justify-content:center;align-items:center}.footer__text{flex-direction:row}.footer__text div{padding-right:5px}}

